.header {
	width: 100%;
	height: 90px;
	font-size: 1em;
}
.header__nav {
	height: 60px;
	display: flex;
	list-style-type: none;
	padding: 0px;
	margin: 0px;
}
.header__nav_el {
	line-height: 60px;
	padding: 0px;
	margin: 0px;
	width: 20%;
	text-align: center;
	text-decoration: none;
}
.header__nav_el:first-child {
	width: 15%;
	margin-left: 5%;
}
.header__nav_el:last-child {
	width: 15%;
	margin-right: 5%;
}
.header__nav_el:hover .t {
	padding: 2px;
	background-color: black;
	color: white;
	cursor: pointer;
	border-radius: 3px;
}
.select  .t {
	padding: 2px;
	background-color: black;
	color: white;
	border-radius: 3px;
}
.underLine {
	text-align: center;
	width: 80%;
	border: 1px solid black;
	background-color: black;
}
.main {
	font-family: monospace;
	width: 80%;
	margin: auto;
	margin-top: 20px;
}
.home_text {
	margin-left: 10%;
}
.squad {
	margin-top: 100px;
	width: 100%;
	height: 400px;
	background-color: black;
}
.hide {
	display: none;
}
.flex_home {
	display: flex;
}
.flex_home_div {
	width: 50%;
}
.list_skills {
	font-size: 1.3em;
}
.flex_skills {
	display: flex;
}
.flex_skills_div {
	width: 50%;
}
.link_vk {
	color: rgba(0,0,0,0.7);
	text-decoration: none;
}
.link_vk:hover {
	color: black;
	text-decoration: underline;
}
.showNumber:hover {
	cursor: pointer;
	text-decoration: underline;
}
.hideNumber {
	display: none;
}
.num:hover {
	cursor: default;
	text-decoration: none;
}
.text_num {
	color: rgba(0,0,0,0.7);
}
.text_num:hover {
	color: black;
}
.portfolio {
	text-align: center;
}
.portfolio_img {
	width: 70%;
	border-radius: 15px;
	border: 1px solid rgba(0,0,0,0.7);
	box-shadow: 6px 6px 6px rgba(0,0,0,0.7);
	margin: 0 15% 20px 15%;
}
.portfolio_img:first-child {
	margin-top: 20px;
}
@media screen and (max-width: 500px) {
	.header {
		font-size: 0.6em;
	}
	.portfolio_img {
		width: 90%;
		margin: 0 5% 20px 5%;
	}
	.main {
		font-size: 0.7em;
	}
	.squad {
		height: 280px;
	}
	.flex_home {
		display: block;
	}
	.flex_home_div {
		width: 100%;
	}
	.flex_skills {
		display: block;
	}
	.flex_skills_div {
		width: 100%;
	}
}